.navbar{
    display: flex;
    align-items: center;
    justify-content:space-between;
    margin: 20px 170px;

}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 15px;
}
.nav-connect{
    padding: 20px,40px;
    border-radius: 50px;
    cursor: pointer;
    background: linear-gradient(267deg,#da7c25 0.36%,#b923e1 102.06%);
    font-size: 20px;
    transition: 0.5s;
}
.nav-connect:hover{
    transform: scale(1.05);
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    gap:5px;
    cursor: pointer;
}
.anchor-link{
    text-decoration: none;
    color: white;
}
.nav-mob-close{
    display: none;
}
.nav-mob-open{
    display: none;
}
@media(max-width:750px){
    .navbar{
        margin: 10px 30px;
    }
    .nav-connect{
        display: none;
    }
    .nav-mob-open{
        display: block;
        position:fixed;
        right: 30px;
    }
    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left: 290px;
        width: 30px;
    }
    .nav-menu{
        position:fixed;
        flex-direction: column;
        align-items: start;
        top:0;
        gap:30px;
        background-color: #1f0016;
        width: 100px;
        height: 100%;
        z-index: 2;
        transition: 0.5s;
        right: -350px;
    }
    .nav-menu li{
        font-size: 20px;
        padding-left: 60px;
        flex-direction: row;
        gap: 20px;
    }
}
